.info-block__content {
  flex-grow: 1;
  margin-left: 90px;
  font-weight: 300;
}

.info-block__illustration {
  flex-basis: 10%;
  display: flex;
  justify-content: center;
  flex-shrink: 0;
}

.info-block__icon {
  align-self: center;
  width: 100%;
}

.info-block__icon svg {
  height: 100%;
  width: 100%;
}

.info-block__title {
  font-size: 1.25rem;
  font-weight: 400;
  margin-bottom: 1rem;
}

.person {
  display: flex;
  width: 70%;
  position: relative;
}

.person__img {
  position: relative;
  width: 100%;
  height: auto;
  overflow: hidden;
}

.person__img img {
  width: 100%;
  height: auto;
}

.person__info {
  display: flex;
  margin: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - (20px * 2));
  height: calc(100% - (20px * 2));
  background-color: white;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transform-origin: center;
  transform: scale(0.95);
  opacity: 0;
  transition: opacity 0.4s ease, transform 0.4s ease;
}

.person__info-name {
  font-size: 1.5rem;
  font-weight: 400;
  transition: transform 0.2s 0.2s ease-out, opacity 0.2s 0.2s ease-out;
}

.person__info-name:after {
  display: block;
  margin: 10px auto;
  position: relative;
  height: 2px;
  width: 30px;
  content: '';
  background: #979797;
}

.person__info-title {
  color: #979797;
  transition: transform 0.2s 0.3s ease-out, opacity 0.2s 0.3s ease-out;
}

.person__info-title, .person__info-name {
  transform: translateY(10px);
  opacity: 0;
}

.person:hover .person__info {
  transform: scale(1);
  opacity: 1;
}

.person:hover .person__info-title, .person:hover .person__info-name {
  transform: translateY(0px);
  opacity: 1;
}

.person--1::after {
  height: 15px;
  width: 100%;
  left: 20%;
  content: '';
  position: absolute;
  background: #FAF2E8;
  bottom: -7.5px;
}

.person--1::before {
  width: 30%;
  height: 60%;
  z-index: -1;
  background: #F8FAFB;
  content: '';
  position: absolute;
  top: -10%;
  right: -20%;
}

.person--2::before {
  width: 100%;
  height: 40%;
  z-index: -1;
  background: #F8FAFB;
  content: '';
  position: absolute;
  left: 20%;
  bottom: -10%;
}

